<template>
    <TextInput v-model= "search"  :placeholder=placeholderText type="text">
    <template #suffix>
      <FeatherIcon class="w-4" name="search" />
    </template>
</TextInput>
</template>

<script setup>
import { watch,ref } from 'vue';
// nh ngha props v emits
const props = defineProps({
  value: String,
  placeholderText:{
    type: String
  }
});
const emits = defineEmits(['update:modelValue']);
// Bin search c lin kt vi gi tr ca input
const search = ref(props.value);
watch(search, (newValue, oldValue) => {
  emits('update:modelValue', newValue); // Gi emit vi gi tr mi ti component cha
});
</script>